    sumdanwei(number) {
      var num = Number(number);
      var abs = Math.abs(num);
      if (abs < 10000) {
        return num;
      } else if (10000 <= abs && abs < 100000000) {
        return (num / 10000).toFixed(2) + "";
      } else {
        return (num / 100000000).toFixed(2) + "";
      }
    },
