    },
    distime(timestamp, number) {
      let timestamp1;
      if (String(timestamp).length == 10) {
        timestamp1 = Number(timestamp) * 1000;
      } else {
        timestamp1 = Number(timestamp);
      }
      const dateObj = new Date(+timestamp1); // ps, , +
      const year = dateObj.getFullYear(); // 
      const month = dateObj.getMonth() + 1; // 10
      const date = dateObj.getDate(); // getDay()
      const hours =
        dateObj.getHours() >= 10
          ? dateObj.getHours()
          : "0" + dateObj.getHours();
      const minutes =
        dateObj.getMinutes() >= 10
          ? dateObj.getMinutes()
          : "0" + dateObj.getMinutes(); // 
      const seconds =
        dateObj.getSeconds() >= 10
          ? dateObj.getSeconds()
          : "0" + dateObj.getSeconds(); // 

      if (number == "3") {
        return year + "-" + month + "-" + date;
      } else {
        return (
          year +
          "-" +
          month +
          "-" +
          date +
          "\n" +
          hours +
          ":" +
          minutes +
          ":" +
          seconds
        );
      }
    },
    chedan(row) {
      let _this = this;
      _this
