          cancelStrategy1({
            id: row.id
          })
            .then(response => {
              let data = response.data;
              if (data.code == 1) {
                _this.$message({
                  message: data.msg,
                  type: "success"
                });
                _this.getalldata();
              } else {
                _this.$message({
                  message: data.msg,
